// Minimalistic Grid Topology Component
function MinimalisticGridTopology() {
  const nodes = [
    { id: 'solar', name: 'Solar', output: 1245, status: 'optimal', x: 20, y: 30, color: 'emerald' },
    { id: 'wind', name: 'Wind', output: 892, status: 'good', x: 80, y: 30, color: 'cyan' },
    { id: 'substation', name: 'Grid', load: 2650, status: 'normal', x: 50, y: 50, color: 'blue' },
    { id: 'city', name: 'City', demand: 1850, status: 'high', x: 30, y: 75, color: 'orange' },
    { id: 'battery', name: 'Battery', soc: 78, status: 'charging', x: 70, y: 75, color: 'purple' }
  ];

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'optimal': return 'border-green-400 bg-green-500/20';
      case 'good': return 'border-cyan-400 bg-cyan-500/20';
      case 'high': return 'border-orange-400 bg-orange-500/20';
      case 'charging': return 'border-purple-400 bg-purple-500/20';
      default: return 'border-blue-400 bg-blue-500/20';
    }
  };

  return (
    <div className="bg-white/5 backdrop-blur-sm border border-white/10 rounded-2xl p-6">
      <h3 className="text-lg font-bold mb-6 flex items-center">
        <div className="mr-2 text-emerald-400"><BuildingIcon /></div>
        Grid Network
      </h3>
      
      <div className="relative h-64 bg-gradient-to-br from-gray-900/30 to-gray-800/20 rounded-xl overflow-hidden">
        {/* Simple connection lines */}
        <svg className="absolute inset-0 w-full h-full pointer-events-none">
          <defs>
            <linearGradient id="simpleFlow" x1="0%" y1="0%" x2="100%" y2="0%">
              <stop offset="0%" stopColor="#10B981" stopOpacity="0.6"/>
              <stop offset="100%" stopColor="#06B6D4" stopOpacity="0.6"/>
            </linearGradient>
          </defs>
          
          {/* Generation to Grid */}
          <line x1="20%" y1="30%" x2="50%" y2="50%" stroke="url(#simpleFlow)" strokeWidth="2" className="animate-pulse"/>
          <line x1="80%" y1="30%" x2="50%" y2="50%" stroke="url(#simpleFlow)" strokeWidth="2" className="animate-pulse"/>
          
          {/* Grid to Loads */}
          <line x1="50%" y1="50%" x2="30%" y2="75%" stroke="url(#simpleFlow)" strokeWidth="2" className="animate-pulse"/>
          <line x1="50%" y1="50%" x2="70%" y2="75%" stroke="url(#simpleFlow)" strokeWidth="2" className="animate-pulse"/>
        </svg>

        {/* Simplified Nodes */}
        {nodes.map((node) => (
          <div
            key={node.id}
            className={`absolute transform -translate-x-1/2 -translate-y-1/2 ${getStatusColor(node.status)} border-2 rounded-lg p-3 w-20 text-center hover:scale-105 transition-all duration-300`}
            style={{ left: `${node.x}%`, top: `${node.y}%` }}
          >
            <div className="text-xs font-bold text-white mb-1">{node.name}</div>
            <div className="text-lg font-bold text-white">
              {node.output || node.load || node.demand || node.soc}
              {node.soc ? '%' : ''}
            </div>
            <div className="text-xs text-gray-400">
              {node.output ? 'MW' : node.load ? 'MW' : node.demand ? 'MW' : 'SOC'}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

