function EnhancedGridTopology() {
  const nodes = [
    { id: 'solar', type: 'generation', name: 'Solar Farm A', output: 1245, capacity: 1500, efficiency: 83, status: 'optimal', color: 'yellow', x: 15, y: 25 },
    { id: 'wind', type: 'generation', name: 'Wind Farm B', output: 892, capacity: 1200, efficiency: 74, status: 'good', color: 'cyan', x: 85, y: 25 },
    { id: 'gas', type: 'generation', name: 'Gas Plant C', output: 709, capacity: 1000, efficiency: 89, status: 'stable', color: 'gray', x: 50, y: 10 },
    { id: 'substation1', type: 'substation', name: 'Main Substation', load: 2846, capacity: 3500, status: 'normal', color: 'emerald', x: 50, y: 55 },
    { id: 'city', type: 'load', name: 'City Center', demand: 1850, forecast: 1820, status: 'high', color: 'blue', x: 20, y: 85 },
    { id: 'industrial', type: 'load', name: 'Industrial Zone', demand: 980, forecast: 950, status: 'normal', color: 'orange', x: 80, y: 85 },
    { id: 'battery', type: 'storage', name: 'Battery Storage', soc: 78, capacity: 500, power: -180, status: 'charging', color: 'purple', x: 15, y: 55 }
  ];

  const connections = [
    { from: 'solar', to: 'substation1', flow: 1245, capacity: 1500, utilization: 83 },
    { from: 'wind', to: 'substation1', flow: 892, capacity: 1200, utilization: 74 },
    { from: 'gas', to: 'substation1', flow: 709, capacity: 1000, utilization: 71 },
    { from: 'substation1', to: 'city', flow: 1850, capacity: 2000, utilization: 92 },
    { from: 'substation1', to: 'industrial', flow: 980, capacity: 1200, utilization: 82 },
    { from: 'battery', to: 'substation1', flow: 180, capacity: 500, utilization: 36 }
  ];

  const getNodeIcon = (type: string) => {
    switch (type) {
      case 'generation': return <BoltIcon />;
      case 'substation': return <ElectricalIcon />;
      case 'load': return <BuildingIcon />;
      case 'storage': return <BatteryIcon />;
      default: return <BoltIcon />;
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'optimal': return 'border-green-400 bg-green-500/20';
      case 'good': return 'border-blue-400 bg-blue-500/20';
      case 'high': return 'border-yellow-400 bg-yellow-500/20';
      case 'charging': return 'border-purple-400 bg-purple-500/20';
      case 'stable': return 'border-gray-400 bg-gray-500/20';
      case 'normal': return 'border-emerald-400 bg-emerald-500/20';
      default: return 'border-gray-400 bg-gray-500/20';
    }
  };

  const getConnectionColor = (utilization: number) => {
    if (utilization > 90) return '#EF4444'; // red
    if (utilization > 75) return '#F59E0B'; // yellow
    return '#10B981'; // green
  };

  return (
    <div className="bg-white/5 backdrop-blur-sm border border-white/10 rounded-2xl p-6">
      <div className="flex items-center justify-between mb-6">
        <h3 className="text-xl font-bold flex items-center">
          <div className="mr-2 text-emerald-400"><BuildingIcon /></div>
          Grid Network Topology
        </h3>
        <div className="flex items-center space-x-4 text-xs">
          <div className="flex items-center space-x-1">
            <div className="w-2 h-2 bg-green-400 rounded-full"></div>
            <span className="text-gray-400">Normal</span>
          </div>
          <div className="flex items-center space-x-1">
            <div className="w-2 h-2 bg-yellow-400 rounded-full"></div>
            <span className="text-gray-400">High Load</span>
          </div>
          <div className="flex items-center space-x-1">
            <div className="w-2 h-2 bg-red-400 rounded-full"></div>
            <span className="text-gray-400">Critical</span>
          </div>
        </div>
      </div>

      <div className="relative h-96 bg-gradient-to-br from-gray-900/50 to-gray-800/30 rounded-xl overflow-hidden border border-white/5">
        {/* Grid Background */}
        <div className="absolute inset-0 opacity-20">
          <svg width="100%" height="100%">
            <defs>
              <pattern id="grid" width="40" height="40" patternUnits="userSpaceOnUse">
                <path d="M 40 0 L 0 0 0 40" fill="none" stroke="#374151" strokeWidth="1"/>
              </pattern>
            </defs>
            <rect width="100%" height="100%" fill="url(#grid)" />
          </svg>
        </div>

        {/* Connection Lines */}
        <svg className="absolute inset-0 w-full h-full pointer-events-none">
          <defs>
            {connections.map((conn, index) => (
              <linearGradient key={index} id={`flow-${index}`} x1="0%" y1="0%" x2="100%" y2="0%">
                <stop offset="0%" stopColor={getConnectionColor(conn.utilization)} stopOpacity="0.8"/>
                <stop offset="50%" stopColor={getConnectionColor(conn.utilization)} stopOpacity="1"/>
                <stop offset="100%" stopColor={getConnectionColor(conn.utilization)} stopOpacity="0.8"/>
              </linearGradient>
            ))}
          </defs>
          
          {connections.map((conn, index) => {
            const fromNode = nodes.find(n => n.id === conn.from);
            const toNode = nodes.find(n => n.id === conn.to);
            if (!fromNode || !toNode) return null;
            
            return (
              <g key={index}>
                <line
                  x1={`${fromNode.x}%`}
                  y1={`${fromNode.y}%`}
                  x2={`${toNode.x}%`}
                  y2={`${toNode.y}%`}
                  stroke={`url(#flow-${index})`}
                  strokeWidth={Math.max(2, conn.utilization / 20)}
                  className="animate-pulse"
                  style={{ animationDuration: `${2 + Math.random()}s` }}
                />
                
                {/* Flow indicator */}
                <circle
                  cx={`${(fromNode.x + toNode.x) / 2}%`}
                  cy={`${(fromNode.y + toNode.y) / 2}%`}
                  r="3"
                  fill={getConnectionColor(conn.utilization)}
                  className="animate-pulse"
                >
                  <animate
                    attributeName="r"
                    values="2;4;2"
                    dur="2s"
                    repeatCount="indefinite"
                  />
                </circle>
                
                {/* Flow value label */}
                <text
                  x={`${(fromNode.x + toNode.x) / 2}%`}
                  y={`${(fromNode.y + toNode.y) / 2 - 3}%`}
                  textAnchor="middle"
                  className="text-xs fill-white font-medium"
                  style={{ fontSize: '10px' }}
                >
                  {conn.flow}MW
                </text>
              </g>
            );
          })}
        </svg>

        {/* Nodes */}
        {nodes.map((node, index) => (
          <div
            key={node.id}
            className={`absolute transform -translate-x-1/2 -translate-y-1/2 ${getStatusColor(node.status)} border-2 rounded-xl p-4 w-32 hover:scale-105 transition-all duration-300 group shadow-lg`}
            style={{ left: `${node.x}%`, top: `${node.y}%` }}
          >
            {/* Node Header */}
            <div className="flex items-center justify-between mb-3">
              <div className="flex items-center space-x-2">
                <div className="w-5 h-5 text-white">{getNodeIcon(node.type)}</div>
                <div className={`w-2 h-2 rounded-full ${
                  node.status === 'optimal' || node.status === 'good' ? 'bg-green-400 animate-pulse' :
                  node.status === 'high' ? 'bg-yellow-400 animate-pulse' :
                  node.status === 'charging' ? 'bg-purple-400 animate-pulse' :
                  'bg-emerald-400'
                }`}></div>
              </div>
              <div className={`text-xs px-2 py-1 rounded-full font-medium ${
                node.status === 'optimal' ? 'bg-green-500/30 text-green-300' :
                node.status === 'good' ? 'bg-blue-500/30 text-blue-300' :
                node.status === 'high' ? 'bg-yellow-500/30 text-yellow-300' :
                node.status === 'charging' ? 'bg-purple-500/30 text-purple-300' :
                'bg-emerald-500/30 text-emerald-300'
              }`}>
                {node.status.toUpperCase()}
              </div>
            </div>

            {/* Node Info */}
            <div className="text-center space-y-2">
              <div className="font-bold text-white text-sm leading-tight">{node.name}</div>
              
              {node.type === 'generation' && (
                <div className="space-y-1">
                  <div className="text-xl font-bold text-white">{node.output}</div>
                  <div className="text-gray-400 text-xs">MW / {node.capacity} MW</div>
                  <div className="text-cyan-400 text-xs font-medium">{node.efficiency}% Efficiency</div>
                </div>
              )}
              
              {node.type === 'load' && (
                <div className="space-y-1">
                  <div className="text-xl font-bold text-white">{node.demand}</div>
                  <div className="text-gray-400 text-xs">MW Demand</div>
                  <div className="text-blue-400 text-xs font-medium">Est: {node.forecast} MW</div>
                </div>
              )}
              
              {node.type === 'storage' && (
                <div className="space-y-1">
                  <div className="text-xl font-bold text-white">{node.soc}%</div>
                  <div className="text-gray-400 text-xs">State of Charge</div>
                  <div className="text-purple-400 text-xs font-medium">{Math.abs(node.power!)} MW {node.power! < 0 ? 'Out' : 'In'}</div>
                </div>
              )}
              
              {node.type === 'substation' && (
                <div className="space-y-1">
                  <div className="text-xl font-bold text-white">{node.load}</div>
                  <div className="text-gray-400 text-xs">MW / {node.capacity} MW</div>
                  <div className="text-emerald-400 text-xs font-medium">{Math.round((node.load! / node.capacity!) * 100)}% Load</div>
                </div>
              )}
            </div>

            {/* Hover Details */}
            <div className="absolute left-full top-0 ml-3 bg-gray-900/95 backdrop-blur-sm border border-white/20 rounded-lg p-4 min-w-[200px] opacity-0 group-hover:opacity-100 transition-opacity duration-300 pointer-events-none z-20 shadow-xl">
              <div className="text-sm space-y-2">
                <div className="font-bold text-white border-b border-white/20 pb-2">{node.name}</div>
                <div className="space-y-1 text-xs">
                  <div className="text-gray-300">Type: <span className="text-white">{node.type}</span></div>
                  <div className="text-gray-300">Status: <span className="text-white">{node.status}</span></div>
                  {node.efficiency && <div className="text-gray-300">Efficiency: <span className="text-cyan-400">{node.efficiency}%</span></div>}
                  {node.capacity && <div className="text-gray-300">Capacity: <span className="text-white">{node.capacity} MW</span></div>}
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Legend */}
      <div className="mt-6 flex items-center justify-between text-xs text-gray-400 border-t border-white/10 pt-4">
        <div className="flex items-center space-x-6">
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 text-yellow-400"><BoltIcon /></div>
            <span>Generation</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 text-emerald-400"><ElectricalIcon /></div>
            <span>Substation</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 text-blue-400"><BuildingIcon /></div>
            <span>Load Centers</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 text-purple-400"><BatteryIcon /></div>
            <span>Storage</span>
          </div>
        </div>
        <div className="text-emerald-400 font-medium">
          Live Network Status: {new Date().toLocaleTimeString()}
        </div>
      </div>
    </div>
  );
}

